      </template>
    </new-layout>

  </div>
</template>

<script>
  import { Tab, Tabs } from 'vant';
  import high from '@/components/encyclopedias/high'
  import low from '@/components/encyclopedias/low'
  export default {
    data(){
      return {
        activeNames:['0'],
        res:'',
        dd_lArray:[],
        dd_dArray:[],
        dd_saArray:[],
        dd_mArray:[],
        dd_nArray:[],
        dd_cArray:[],//病因
        dd_sArray:[],
        dd_rArray:[],
        dd_gArray:[],
        dd_dArray1:[],
        disease:':',
        classification:'',
        reason:'',
        dd_cArrayShow:false,
        dd_sArrayShow:false,
        dd_rArrayShow:false,
        dd_lArrayShow:false,
        dd_dArrayShow:false,
        dd_dArray1Show:false,
        dd_mArrayShow:false,
        dd_saArrayShow:false,
        dd_nArrayShow:false,
        dd_gArrayShow:false,
        highcomponent:true,//
        zhibiaoxiangqing: false,
        piangaopiandi: false,
        gao: false,
        highlowArray: [
          {
            index:0,
            value: '偏高'
          },
          {
            index:1,
            value: '偏低'
          }
          ], //偏高偏低数组
      }
    },
    components:{
      high,
      low,
      'van-tab':Tab,
      'van-tabs':Tabs,
    },
    methods:{
      highlow(index) {
          this.highcomponent = !this.highcomponent;
      },
      async init(){
        let _this=this;
        let key=this.$route.query.code;
        this.res= await this.$http.get(this.$HOST+'/open/xk/symptom?code='+key);



         if(this.res.result.knowledge.length<=1) {
          this.zhibiaoxiangqing = true;
          this.piangaopiandi = false;
          this.gao = true;
        } else {
           this.zhibiaoxiangqing = false;
           this.piangaopiandi = true;
           this.gao = false;
         }

        if(this.res.result.type == 'index') {
            this.disease = "检查目的：";
            this.classification = "判定标准：";
            this.reason = "可能原因：";
        } else {
          this.disease = "疾病概述：";
          this.classification = "疾病分类：";
          this.reason = "病因";
        }



   for(var i=0;i<this.res.result.knowledge.length;i++) {
     if(this.res.result.knowledge[i].dd_l) {
       this.dd_cArrayShow=true;
       this.common(this.res.result.knowledge[i].dd_l,_this.dd_lArray)
     }

     if(this.res.result.knowledge[i].dd_s) {
       this.dd_sArrayShow=true;
       this.common(this.res.result.knowledge[i].dd_s,_this.dd_sArray)
     }

     if(this.res.result.knowledge[i].dd_l) {
       this.dd_lArrayShow=true;
       this.common(this.res.result.knowledge[i].dd_l,_this.dd_lArray)
     }


     if(this.res.result.knowledge[i].dd_d_s) {
       this.dd_dArrayShow=true;
       this.common(this.res.result.knowledge[i].dd_d_s,_this.dd_dArray)

     }




     if(this.res.result.knowledge[i].dd_d_a) {
       this.dd_dArray1Show=true;
       this.common(this.res.result.knowledge[i].dd_d_a,_this.dd_dArray1)
     }


     if(this.res.result.knowledge[i].dd_sa) {
       this.dd_saArrayShow=true;
       this.common(this.res.result.knowledge[i].dd_sa,_this.dd_saArray)
     }


     if(this.res.result.knowledge[i].dd_m) {
       this.dd_mArrayShow=true;
       this.common(this.res.result.knowledge[i].dd_m,_this.dd_mArray)
     }



     if(this.res.result.knowledge[i].dd_n) {
       this.dd_nArrayShow=true;
       this.common(this.res.result.knowledge[i].dd_n,_this.dd_nArray)
     }



     if(this.res.result.knowledge[i].dd_c) {
       this.dd_cArrayShow=true;
       this.common(this.res.result.knowledge[i].dd_c,_this.dd_cArray)
     }




     if(this.res.result.knowledge[i].dd_r) {
       this.dd_rArrayShow=true;
       this.common(this.res.result.knowledge[i].dd_r,_this.dd_rArray)
     }



     if(this.res.result.knowledge[i].dd_g) {
       this.dd_gArrayShow=true;
       this.common(this.res.result.knowledge[i].dd_g,_this.dd_gArray)
     }




   }







    },
   common(data,arrayname) {
       let resLen=data.split("@#");
        for(var i=0; i<resLen.length; i++){
          if(resLen[i]){
            arrayname.push(resLen[i].split("#@"));
          }
        }
      }
  },
    created(){
      this.init();
    }
  }
</script>

<style scoped lang="scss">
  $height80: 80px;
  $bg-fff: #ffffff;
  #encyclopedias {
    background: #f3f3f3;
    .sugges_li {
      border-bottom: 1px dashed #999999;
      margin-bottom: 12px;
      padding: 10px;
    }
    .title {
      color: orange;
      font-size: 24px;
    }
    p:nth-child(2){
      font-size: 18px;
    }
  }

  table{
    width: 96%;
    margin: auto;
    tr{
      height: 50px;
      line-height: 50px;
      td{
        font-size: 18px;
        font-weight: bold;
      }
      td:nth-child(2){
        font-weight: 100;
        color: #333333;
      }
    }
  }
  .van-cell__title span{
    font-size: 18px!important;
  }
  .width100{
    width: 160px;
    vertical-align: -webkit-baseline-middle;
  }

  header{
    font-size: 30px;
    background: #ffffff;
    text-indent: 18px;
    height: 80px;
    line-height: 80px;
    font-weight: bold;
  }
  .disease_detail{
     color: #089ec3;
    font-size: 15px;
    background: #ffffff;
    text-indent: 18px;
    height: 80px;
    line-height: 80px;
    font-weight: bold;
  }

  .suit{
    width: 30px;
    height: 30px;
    background: #68ba8a;
    display: inline-block;
    text-align: center;
    line-height: 30px;
    color: #ffffff;
    font-size: 14px;
  }
  .avoid{
    width: 30px;
    height: 30px;
    background: #d95d5d;
    display: inline-block;
    text-align: center;
    line-height: 30px;
    color: #ffffff;
    font-size: 14px;
  }

  .fz24{
    font-size: 24px;
  }



  #encyclopedias p{
    font-size: 28px!important;
  }

  #announce_header{
    width: 100%;
    height: 120px;
    color: #FFF;

    text-align: center;
    background: #6EAFFF;
    display: flex;
    align-items: center;
    justify-content: center;

    .iconfont{
      flex: .15;
      font-size: 50px;
      display: flex;
      justify-content: center;
    }

    span{
      flex: 1;
      font-size: 35px;
    }
  }
/deep/.van-tabs__wrap{
    position: static!important;
  }

  table tr td{
    font-size: 30px;
  }
  .disease_detail{
    font-size: 30px;
  }
</style>



